.faq {
    margin-bottom: 130px;
    position: relative;
    background-color: var(--black-white);
    transition: 0.3s ease all;
    padding-top: 130px;
    &__container {
    }

    &__tag {
        top: 10%;
    }

    &__title {
        color: var(--yellow-blue);
    }

    &__columns {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 40px;
        margin-bottom: 40px;
    }

    &__questions {
        display: flex;
        flex-direction: column;
        color: var(--white-navy);
    }

    &__button {
        display: inline-flex;  
        padding: 20px 30px;
        font-size: var(--subtitleSize);
    }

    @media (max-width: 1080px) {
        margin-bottom: 130px;
        padding-top: 70px;
        &__container {
        }

        &__title {
        }

        &__columns {
            grid-template-columns: repeat(1, 1fr);
            gap: 0px;
            margin-bottom: 30px;
        }

        &__questions {
            display: flex;
            flex-direction: column;
        }

        &__button {
            display: inline-block;  
            margin: 0px auto;
        }
    }
}
