:root {
  --montserrat: Montserrat, Raleway;
  --raleway: Raleway;
  --bs-font-size: 16px;
  --bs-font-weight: 400;
  --bs-line-height: 1;
  --text-line-height: 40px;
  --subtitle-line-height: 30px;
  --text-line-height: 30px;
  --card-title-line-height: 30px;
  --min-title-line-height: 30px;
  --mobile-subtitle-line-height: 20px;
  --min-title-mobile-line-height: 25px;
  --text-mobile-line-height: 20px;
  --title-big-size: 64px;
  --about-title: 62px;
  --title-size: 48px;
  --form-size: 40px;
  --cardNumSize: 36px;
  --card-subtitle: 32px;
  --cardTitleSize: 28px;
  --mobileTitle-size: 24px;
  --about-title-mobile: 24px;
  --cardTitleMobileSize: 24px;
