  --numeraSize: 14px;
  --textMobileSize: 14px;
  --textMedMobileSize: 14px;
  --black: #000000;
  --navy: #07202F;
  --blue: #0072FF;
  --lightblue: #3A97ED;
  --grayblue: #2A5E97;
  --yellow: #FFC357;
  --bright-yellow: #FFB927;
  --bg-black: #040B0F;
  --gray-70: rgba(255, 255, 255, 0.7);
  --gray-60: rgba(255, 255, 255, 0.6);
  --bg-white: #fff;
  --font-color: #fff;
  --radius: 15px;
  --white-navy: #fff;
  --white-blue: #fff;
  --white-gray: #AAACAA;
  --white-grayish: #fff;
  --white-blue-05: #fff;
  --white-black: #fff;
  --white-06-navy: #9B9D9F;
  --yellow-blue: #FFC357;
  --yellow-03-navy: #4F4225;
  --yellow-navy: #FFC357;
  --yellow-white: #FFC357;
  --yellow-aaa: #FFC357;
  --grayblue--blue: #2A5E97;
  --navy-white: #07202F;
  --navy-blue-05: #07202F;
  --navy-blue-10: #07202F;
  --navy-blue: #07202F;
  --black-white: #040B0F;
  --gray-60-white: rgba(255, 255, 255, 0.6);
  --gray-70-navy: rgba(255, 255, 255, 0.7);
  --gray-70-blue-50: rgba(255, 255, 255, 0.7);
  --gray-70-555: rgba(255, 255, 255, 0.7);
  --transparent-blue-05: transparent;
  --transparent-blue: transparent;
  --swiper-bg: linear-gradient(90deg, #040B0F 0%, rgba(4, 11, 15, 0.2) 25%, rgba(4, 11, 15, 0) 50%, rgba(4, 11, 15, 0.2) 75%, #040B0F 100%);
  --transition: 0.3s ease all;
  --primary-color: #131313;
  --primary-hover-color: #2d2d2d;
  --secondary-color: #ff4040;
  --white-color: #fff;
  --black-color: #131313;
  --bs-shadow: 0px 2px 3px rgba(0, 0, 0, 0.1), 0px 8px 3px rgba(0, 0, 0, 0.1);
  --hover-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1), 0 0 10px 0 rgba(0, 0, 0, 0.1), 0 0 15px 0 rgba(0, 0, 0, 0.1);
}

