.title {
  font-family: var(--raleway);
  font-size: var(--title-size);
  line-height: 1;
  font-weight: 700;
  color: var(--white-blue);
}
@media (max-width: 1080px) {
  .title {
    font-size: var(--mobileTitle-size);
  }
}
.title span {
  font-style: italic;
  color: var(--yellow);
  font-weight: 600;
}

.title-big {
  font-size: var(--title-big-size);
  line-height: 1.1;
  font-weight: 500;
}
@media (max-width: 1080px) {
  .title-big {
    font-size: var(--mobileTitle-size);
    line-height: 1.2;
  }
}

.numera {
  color: var(--yellow);
  font-size: var(--numeraSize);
}

.text {
  font-size: var(--textSize);
  line-height: var(--text-line-height);
  color: var(--gray-70-navy);
}
@media (max-width: 1080px) {
  .text {
    font-size: var(--textMobileSize);
    line-height: var(--text-mobile-line-height);
  }
}

.min-text {
  font-size: var(--numeraSize);
  line-height: var(--min-title-line-height);
  color: var(--gray-70-blue-50);
}
@media (max-width: 1080px) {
  .min-text {
    line-height: var(--min-title-mobile-line-height);
  }
}

.tag {
  color: var(--navy-white);
  writing-mode: vertical-rl; /* или vertical-lr */
  padding: 10px 8px;
  background-color: var(--yellow-blue);
  border-radius: 0px 10px 10px 0px;
  position: absolute;
  right: 0px;
  transform: rotate(180deg);
  top: 15%;
}
@media (max-width: 1080px) {
  .tag {
    display: none;
  }
}

.card-title {
  font-size: var(--cardTitleSize);
  line-height: var(--card-title-line-height);
  font-weight: 600;
}
@media (max-width: 1080px) {
  .card-title {
    font-size: var(--cardTitleMobileSize);
  }
}

.button {
  border-radius: var(--radius);
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: var(--blue);
  text-align: center;
}

.mobile-only {
  display: none;
}

.yellow-button {
  background-color: var(--bright-yellow);
  height: 61px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 20px;
  color: var(--blue);
  display: inline-flex;
  border-radius: var(--radius);
  font-weight: 500;
  cursor: pointer;
  user-select: none;
}

.yellow-button-big {
  font-size: var(--cardNumSize);
  background-color: var(--bright-yellow);
  height: 120px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: var(--navy);
  border-radius: 30px;
  font-weight: 500;
  cursor: pointer;
  user-select: none;
  max-width: 710px;
  width: 100%;
}
@media (max-width: 1080px) {
  .yellow-button-big {
    font-size: var(--textMobileSize);
    height: 50px;
    border-radius: 20px;
    max-width: 231px;
    width: 100%;
  }
}

.button-transparent {
  font-size: var(--textSize);
  color: #fff;
  height: 60px;
  border: 1px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  padding: 0px 83px;
}

body.light .only-dark {
  display: none;
}

.only-light {
  display: none;
}

body.light .only-light {
  display: block;
}

@media (max-width: 1080px) {
  .mobile-only {
    display: inherit;
  }
  .desktop-only {
    display: none;
  }
}
.dropdown-simple_list {
  display: none;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 2;
  padding: 20px 0px;
  background-color: var(--navy-white);
  transition: 0.3s ease all;
}
.header.scrolled {
  background-color: rgba(7, 32, 47, 0.7725490196);
  backdrop-filter: blur(10px);
}
.header.about-header {
  background-color: transparent;
}
.header.about-header.scrolled {
  background-color: rgba(0, 0, 0, 0.7607843137);
  backdrop-filter: blur(10px);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__nav {
  flex: 1 1 auto;
  max-width: 630px;
}
.header__nav_list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__nav_item a {
  color: var(--white-navy);
  transition: 0.3s ease all;
}
.header__nav .header__arrow {
  display: none;
}
.header__lang {
  display: flex;
  align-items: center;
  gap: 15px;
}
.header__lang_item {
  display: flex;
  align-items: center;
  gap: 5px;
  color: var(--white-navy);
  transition: 0.3s ease all;
  cursor: pointer;
}
.header__lang_item.active {
  border: 1px solid var(--white-blue);
  border-radius: var(--radius);
  padding: 10px 15px;
}
.header__theme {
  cursor: pointer;
  user-select: none;
}
.header__contact {
  color: var(--yellow-blue);
  border-radius: var(--radius);
  border: 1px solid var(--yellow-blue);
  height: 61px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 20px;
  transition: 0.3s ease all;
  cursor: pointer;
  user-select: none;
}
@media (max-width: 1080px) {
  .header {
    background-color: var(--black-white);
  }
  .header__logo {
    max-width: 120px;
  }
  .header__logo svg {
    width: 100%;
  }
  .header__nav {
    flex: inherit;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 999;
    background-color: var(--navy);
    transition: 0.3s ease all;
  }
  .header__nav_item a {
    color: #fff;
    transition: 0.3s ease all;
  }
  .header__nav_list {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 50px;
    justify-content: space-between;
    width: 232px;
    height: 381px;
    padding: 50px 0px;
  }
  .header__nav .header__arrow {
    display: block;
    position: fixed;
    top: auto;
    left: 231px;
    width: 30px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--navy);
    border-radius: 0px 30px 30px 0px;
    transition: 0.3s ease all;
  }
  .header__nav .header__arrow svg {
    transform: rotate(0deg);
    transition: 0.3s ease all;
  }
  .header__nav.active {
    left: -232px;
  }
  .header__nav.active .header__arrow {
    left: 0;
    z-index: 1;
  }
  .header__nav.active .header__arrow svg {
    transform: rotate(180deg);
  }
  .header__lang {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  .header__lang_item {
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
  }
  .header__lang_item.active {
    border-radius: var(--radius);
    padding: 10px 15px;
  }
  .header__theme {
    cursor: pointer;
  }
  .header__contact {
    padding: 20px;
    color: var(--yellow);
    border: 1px solid var(--yellow);
    border-radius: var(--radius);
    cursor: pointer;
    display: none;
  }
}

body.light .header.scrolled {
  background-color: rgba(255, 255, 255, 0.7921568627);
  backdrop-filter: blur(10px);
}
body.light .header.about-header {
  background-color: transparent;
}
body.light .header.about-header.scrolled {
  background-color: rgba(255, 255, 255, 0.7921568627);
  backdrop-filter: blur(10px);
}

.main {
  background-color: var(--navy-white);
  padding-bottom: 140px;
  transition: 0.3s ease all;
}
.main__container {
  display: flex;
  gap: 20px;
}
.main__box {
  position: relative;
  flex: 0 1 69%;
  background-color: var(--blue);
  border-radius: var(--radius);
  padding: 85px 50px 74px;
}
.main__box_3d {
  position: absolute;
  bottom: -17%;
  right: -9%;
  width: 52%;
}
.main__title {
  max-width: 660px;
  color: #fff;
}
.main__subtitle {
  margin: 45px 0px 30px;
  font-size: var(--subtitleSize);
  line-height: var(--subtitle-line-height);
}
.main__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.main__list_item {
  display: flex;
  align-items: center;
  gap: 10px;
}
.main__contact {
  padding: 20px;
  border-radius: var(--radius);
  border: 1px solid #fff;
  display: inline-block;
  margin-top: 30px;
  font-weight: 500;
  cursor: pointer;
  user-select: none;
}
.main__img {
  flex: 1 1 auto;
  border-radius: var(--radius);
  overflow: hidden;
}
.main__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 1080px) {
  .main {
    background-color: var(--black-white);
    padding-bottom: 0px;
  }
  .main__container {
    flex-direction: column;
    gap: 30px;
    overflow: hidden;
  }
  .main__box {
    flex: 1 1 auto;
    padding: 20px;
  }
  .main__box > img {
    position: absolute;
    bottom: -5%;
    right: -7%;
    width: 32%;
  }
  .main__title {
    max-width: 660px;
  }
  .main__subtitle {
    margin: 20px 0px;
    font-size: var(--mobileSubtitleSize);
    line-height: var(--mobile-subtitle-line-height);
  }
  .main__list_item br {
    display: none;
  }
  .main__contact {
    padding: 17px 18px;
    background-color: var(--yellow);
    color: var(--navy);
    border: none;
    margin-top: 20px;
  }
  .main__img {
    height: 250px;
  }
  .main__img img {
    height: 450px;
  }
}

.we {
  margin: 130px 0px;
  background-color: var(--black-white);
  transition: 0.3s ease all;
  padding: 113px 0px 150px;
  overflow: hidden;
  position: relative;
}
.we__container {
  display: flex;
  gap: 40px;
}
.we__img {
  position: relative;
  flex: 0 1 45%;
}
.we__img_transparent {
  border-radius: var(--radius);
  z-index: 1;
  opacity: 0.4;
}
.we__img_main {
  position: absolute;
  right: 30px;
  top: -94px;
  border-radius: var(--radius);
  border: 2px solid var(--yellow);
  z-index: 1;
}
.we__img_uzb {
  z-index: 0;
}
.we__img svg {
  position: absolute;
  bottom: -370px;
  right: -40%;
}
.we__content {
  flex: 0 1 55%;
  z-index: 1;
}
.we__title {
  max-width: 496px;
  margin: 20px 0px 30px;
}
@media (max-width: 1080px) {
  .we {
    margin: 0px 0px;
    padding: 80px 0px;
    overflow: hidden;
  }
  .we__container {
    flex-direction: column-reverse;
    gap: 30px;
  }
  .we__img {
    flex: inherit;
    text-align: center;
  }
  .we__img_transparent {
    width: 80%;
    position: relative;
    z-index: 1;
  }
  .we__img_main {
    right: inherit;
    left: 15%;
    top: -20px;
    height: 150%;
  }
  .we__img svg {
    width: 110%;
    bottom: -200%;
    left: 0%;
    z-index: 0;
  }
  .we__content {
    flex: inherit;
    text-align: center;
  }
  .we__title {
    max-width: 100%;
    margin: 10px auto 20px auto;
  }
}

body.light .we__img_uzb ellipse[fill="#F2F1EC"] {
  fill: #0072FF;
  opacity: 0.6;
}

.source {
  padding: 54px 0px;
  background-color: var(--navy-white);
  position: relative;
}
.source__title {
  margin: 20px 0px 70px 0px;
  max-width: 542px;
}
.source__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 75px;
  row-gap: 40px;
